body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu Mono", monospace;
  letter-spacing: 1px;
  background: -webkit-linear-gradient(top, #777 0%, #333 24%, #111 51%, #111 51%, #333 80%, #777 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#777), color-stop(24%, #333), color-stop(51%, #111), color-stop(51%, #111), color-stop(80%, #333), to(#777));
  background: -o-linear-gradient(top, #777 0%, #333 24%, #111 51%, #111 51%, #333 80%, #777 100%);
  background: linear-gradient(to bottom, #777 0%, #333 24%, #111 51%, #111 51%, #333 80%, #777 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#999999', endColorstr='#777777',GradientType=0 );
  background-size: 100% 110%;
  -webkit-animation: AnimationName 20s ease-in-out infinite;
  animation: AnimationName 20s ease-in-out infinite;
  background-attachment: fixed; }

@-webkit-keyframes AnimationName {
  0% {
    background-position: 52% 0%; }
  50% {
    background-position: 49% 100%; }
  100% {
    background-position: 52% 0%; } }

@keyframes AnimationName {
  0% {
    background-position: 52% 0%; }
  50% {
    background-position: 49% 100%; }
  100% {
    background-position: 52% 0%; } }

.no-select {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }

.hide {
  visibility: hidden; }

svg.nav {
  width: 48px;
  -webkit-animation: btn-opacity 2s infinite;
          animation: btn-opacity 2s infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  cursor: pointer; }

@-webkit-keyframes btn-opacity {
  0% {
    opacity: 0.6; }
  50% {
    opacity: 1.0; }
  100% {
    opacity: 0.6; } }

@keyframes btn-opacity {
  0% {
    opacity: 0.6; }
  50% {
    opacity: 1.0; }
  100% {
    opacity: 0.6; } }



